<h3>单独引用</h3>
<p>单独引入 <code v-pre="" class="code_inline">TAlert</code>：</p>
<pre v-pre=""><code v-pre="" class="language-javascript"><span class="hljs-keyword">import</span> { TModal } <span class="hljs-keyword">from</span> <span class="hljs-string">'tty-ui'</span>;

</code></pre>
<h3>具体细节设置，请阅读下面的 api</h3>
<h3>Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>类型</th>
<th>默认值</th>
<th>必需</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>Boolean</td>
<td>false</td>
<td>是</td>
<td>显示/隐藏模态框</td>
</tr>
<tr>
<td>title</td>
<td>String</td>
<td>-</td>
<td>否</td>
<td>模态框标题（如果有title插槽会被覆盖）</td>
</tr>
<tr>
<td>width</td>
<td>String</td>
<td>-</td>
<td>否</td>
<td>自定义模态框的宽度，例如 <code v-pre="" class="code_inline">500px</code>、<code v-pre="" class="code_inline">80%</code></td>
</tr>
<tr>
<td>size</td>
<td>String</td>
<td>-</td>
<td>否</td>
<td>设置模态框尺寸。支持 <code v-pre="" class="code_inline">lg</code>或<code v-pre="" class="code_inline">sm</code></td>
</tr>
<tr>
<td>backdrop</td>
<td>Boolean</td>
<td>false</td>
<td>否</td>
<td>设置点击模态框区域外是否隐藏模块框</td>
</tr>
<tr>
<td>header</td>
<td>Boolean</td>
<td>true</td>
<td>否</td>
<td>是否显示头部</td>
</tr>
<tr>
<td>footer</td>
<td>Boolean</td>
<td>true</td>
<td>否</td>
<td>是否显示尾部</td>
</tr>
<tr>
<td>cancel-text</td>
<td>String</td>
<td>取消</td>
<td>否</td>
<td>覆盖取消按钮显示文本</td>
</tr>
<tr>
<td>ok-text</td>
<td>String</td>
<td>确定</td>
<td>否</td>
<td>覆盖确定按钮显示文本</td>
</tr>
<tr>
<td>transition-duration</td>
<td>Number</td>
<td>150</td>
<td>否</td>
<td>动画持续时间，设置0关闭动画</td>
</tr>
<tr>
<td>auto-focus</td>
<td>Boolean</td>
<td>false</td>
<td>否</td>
<td>自动聚焦到设置了 data-action=<code v-pre="" class="code_inline">&quot;auto-focus&quot;</code> 属性的按钮，默认是确定按钮</td>
</tr>
<tr>
<td>keyboard</td>
<td>Boolean</td>
<td>false</td>
<td>否</td>
<td>按esc键可以关闭模态框</td>
</tr>
<tr>
<td>vertical-align</td>
<td>String</td>
<td>-</td>
<td>否</td>
<td>设置垂直方向位置 'top','middle','bottom'</td>
</tr>
<tr>
<td>show-confirm-button</td>
<td>Boolean</td>
<td>true</td>
<td>否</td>
<td>设置是否显示确认按钮</td>
</tr>
<tr>
<td>show-cancel-button</td>
<td>Boolean</td>
<td>true</td>
<td>否</td>
<td>设置是否显示取消按钮</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>替代默认title</td>
</tr>
<tr>
<td>default</td>
<td>替代默认body</td>
</tr>
<tr>
<td>header</td>
<td>替代默认header。注：会同时替换title插槽的内容</td>
</tr>
<tr>
<td>footer</td>
<td>替代默认footer</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>参数</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>cancel-click</td>
<td>-</td>
<td>点取消按钮的回掉操作</td>
</tr>
<tr>
<td>ok-click</td>
<td>-</td>
<td>点确定按钮的回掉操作</td>
</tr>
<tr>
<td>show</td>
<td>-</td>
<td>模态框显示后触发</td>
</tr>
<tr>
<td>hide</td>
<td>msg</td>
<td>模态框隐藏时触发，msg: <code v-pre="" class="code_inline">''</code>或<code v-pre="" class="code_inline">ok</code>或<code v-pre="" class="code_inline">cancel</code></td>
</tr>
</tbody>
</table>
</section></template>

<script>
  module.exports = {
    data () {
      return {
        show: false,
        show1: false,
        show3: false,
        show4: false,
        show5: false,
        show6: false,
      }
    },
    methods: {
      open() {
        this.show = !this.show
      },
      open2() {
        this.show1 = !this.show1
      },
      open3() {
        this.show3 = !this.show3
      },
      open4() {
        this.show4 = !this.show4
      },
      open5() {
        this.show5 = !this.show5
      },
      open6() {
        this.show6 = !this.show6
      },
    }
  };
</script>


