</div></demo-block>
<h3>禁用状态</h3>
<p>通过 <code v-pre="" class="code_inline">disabled</code> 属性指定是否禁用 input 组件</p>
<div class="demo-block">
  <t-input v-model="input1" size="sm" :disabled="true"></t-input>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <t-input v-model="input1" size="sm" :disabled="true"></t-input>
</div>



</body></html></div>
