      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">msg</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>文本域</h3>
<p>用于输入多行文本信息，通过将 <code v-pre="" class="code_inline">type</code> 属性的值指定为 textarea。</p>
<div class="demo-block">
  <t-input type="textarea" :rows="2" placeholder="请输入内容" v-model="input2"></t-input>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <t-input type="textarea" :rows="2" placeholder="请输入内容" v-model="textarea"></t-input>
</div>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-input</span>
    <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span>
    <span class="hljs-attr">:rows</span>=<span class="hljs-string">"2"</span>
    <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"textarea"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-input</span>&gt;</span>
