    <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prepend"</span>&gt;</span>Http://<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">t-input</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"msg"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"append"</span>&gt;</span>.com<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">t-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">msg</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>尺寸</h3>
<p>可通过 <code v-pre="" class="code_inline">size</code> 属性指定输入框的尺寸，除了默认的大小外，还提供了 lg、sm尺寸。</p>
<div class="demo-block">
  <t-input v-model="input5" size="sm"></t-input>
  <br>
  <t-input v-model="input6" size="lg"></t-input>
  <br>
  <t-input v-model="input7"></t-input>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <t-input v-model="msg" size="sm"></t-input>
  <t-input v-model="msg" size="lg"></t-input>
  <t-input v-model="msg"></t-input>
</div>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"msg"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"sm"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-input</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"msg"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"lg"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-input</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"msg"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
