<template><section>
<h1>Checkbox 复选框</h1>
<hr>
<h3>基础用法</h3>
<p>单独使用可以表示两种状态之间的切换，写在标签中的内容为 checkbox 按钮后的介绍。</p>
<div class="demo-block">
  <t-checkbox v-model="checkbox1">备选项</t-checkbox>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <t-checkbox v-model="checkbox1">备选项</t-checkbox>
</div>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkbox1"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checkbox1</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>禁用状态</h3>
<p>设置<code v-pre="" class="code_inline">disabled</code>属性即可。</p>
<div class="demo-block">
  <t-checkbox v-model="checkbox1" disabled>备选项</t-checkbox>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <t-checkbox v-model="checkbox1" disabled>备选项</t-checkbox>
</div>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkbox1"</span> <span class="hljs-attr">disabled</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checkbox1</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>多选框组</h3>
<p><code v-pre="" class="code_inline">checkbox-group</code>元素能把多个 checkbox 管理为一组，只需要在 Group 中使用<code v-pre="" class="code_inline">v-model</code>绑定<code v-pre="" class="code_inline">Array</code>类型的变量即可。 <code v-pre="" class="code_inline">t-checkbox</code> 的 <code v-pre="" class="code_inline">label</code>属性是该 checkbox 对应的值，若该标签中无内容，则该属性也充当 checkbox 按钮后的介绍。<code v-pre="" class="code_inline">label</code>与数组中的元素值相对应，如果存在指定的值则为选中状态，否则为不选中。</p>
<div class="demo-block">
  <t-checkbox-group v-model="checkList">
    <t-checkbox label="复选框 A"></t-checkbox>
    <t-checkbox label="复选框 B"></t-checkbox>
    <t-checkbox label="复选框 C"></t-checkbox>
    <t-checkbox label="禁用" disabled></t-checkbox>
    <t-checkbox label="选中且禁用" disabled></t-checkbox>
  </t-checkbox-group>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <t-checkbox-group v-model="checkList">
    <t-checkbox label="复选框 A"></t-checkbox>
    <t-checkbox label="复选框 B"></t-checkbox>
    <t-checkbox label="复选框 C"></t-checkbox>
    <t-checkbox label="禁用" disabled></t-checkbox>
    <t-checkbox label="选中且禁用" disabled></t-checkbox>
  </t-checkbox-group>
</div>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkList"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框 A"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框 B"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框 C"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"禁用"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选中且禁用"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checkList</span>: []
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>可选项目数量的限制</h3>
<p>使用 <code v-pre="" class="code_inline">min</code> 和 <code v-pre="" class="code_inline">max</code> 属性能够限制可以被勾选的项目的数量。</p>
<div class="demo-block">
  <t-checkbox-group v-model="checkedCities1" :min="1" :max="2">
    <t-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</t-checkbox>
  </t-checkbox-group>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><t-checkbox-group v-model="checkedCities1" :min="1" :max="2">
  <t-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</t-checkbox>
</t-checkbox-group>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox-group</span> 
  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkedCities1"</span>
  <span class="hljs-attr">:min</span>=<span class="hljs-string">"1"</span>
