  <span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"city in cities"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"city"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"city"</span>&gt;</span>{{city}}<span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox</span>&gt;</span>
