    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checkedCities1</span>: [<span class="hljs-string">'上海'</span>, <span class="hljs-string">'北京'</span>],
        <span class="hljs-attr">cities</span>: [<span class="hljs-string">'上海'</span>, <span class="hljs-string">'北京'</span>, <span class="hljs-string">'广州'</span>, <span class="hljs-string">'深圳'</span>]
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>按钮样式</h3>
<p>按钮样式的多选组合。
只需要把<code v-pre="" class="code_inline">type</code> 加上<code v-pre="" class="code_inline">button</code>即可</p>
<div class="demo-block">
  <t-checkbox-group v-model="checkedRoles" type="button" tips="至少选择一种角色">
    <t-checkbox v-for="role in roles" :label="role.label" :key="role.value">{{role.label}}</t-checkbox>
  </t-checkbox-group>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><t-checkbox-group v-model="checkedRoles" type="button" tips="至少选择一种角色">
  <t-checkbox v-for="role in roles" :label="role.label" :key="role.value">{{role.label}}</t-checkbox>
</t-checkbox-group>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkedRoles"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">tips</span>=<span class="hljs-string">"至少选择一种角色"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-checkbox</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"role in roles"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"role.label"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"role.value"</span>&gt;</span>{{role.label}}<span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">t-checkbox-group</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
