<h3>Options or Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>类型</th>
<th>默认参数</th>
<th>是否必须</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>Number</td>
<td>-</td>
<td>是</td>
<td>当前页码</td>
</tr>
<tr>
<td>boundary-links</td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>显示First / Last 按钮</td>
</tr>
<tr>
<td>direction-links</td>
<td>Boolean</td>
<td></td>
<td></td>
