    <t-radio>默认单选</t-radio>
  </div>
  <br>
  <div class="m-10">
    <t-radio v-model="radio1" label="男">男</t-radio>
    <t-radio v-model="radio1" label="女">女</t-radio>
    <p>{{radio1}}</p>
  </div>
</div>
