<template><section>
<h1>Alert 消息提示</h1>
<hr>
<h3>基础用法</h3>
<p>从顶部出现，3 秒后自动消失。</p>
<div class="demo-block">
  <t-button :plain="true" @click="open">打开消息提示</t-button>
  <t-button :plain="true" @click="open2">打开消息提示</t-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <t-button :plain="true" @click="open">打开消息提示</t-button>
  <t-button :plain="true" @click="open2">打开消息提示</t-button>
</div>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open"</span>&gt;</span>打开消息提示<span class="hljs-tag">&lt;/<span class="hljs-name">t-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open2"</span>&gt;</span>打开消息提示<span class="hljs-tag">&lt;/<span class="hljs-name">t-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

