</div></demo-block>
<h3>不同状态</h3>
<p>用来显示「成功、警告、消息、错误」类的操作反馈。
当需要自定义更多属性时，Alert也可以接收一个对象为参数。比如，设置<code v-pre="" class="code_inline">type</code>字段可以定义不同的状态，默认为<code v-pre="" class="code_inline">info</code>。此时正文内容以<code v-pre="" class="code_inline">message</code>的值传入。同时，我们也为 Message 的各种 type 注册了方法，可以在不传入<code v-pre="" class="code_inline">type</code>字段的情况下像<code v-pre="" class="code_inline">open4</code>那样直接调用。</p>
<div class="demo-block">
  <t-button :plain="true" @click="open2">成功</t-button>
  <t-button :plain="true" @click="open3">警告</t-button>
  <t-button :plain="true" @click="open">消息</t-button>
  <t-button :plain="true" @click="open4">错误</t-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <t-button :plain="true" @click="open2">成功</t-button>
  <t-button :plain="true" @click="open3">警告</t-button>
  <t-button :plain="true" @click="open">消息</t-button>
  <t-button :plain="true" @click="open8">错误</t-button>
</div>




</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open2"</span>&gt;</span>成功<span class="hljs-tag">&lt;/<span class="hljs-name">t-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open3"</span>&gt;</span>警告<span class="hljs-tag">&lt;/<span class="hljs-name">t-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open"</span>&gt;</span>消息<span class="hljs-tag">&lt;/<span class="hljs-name">t-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">t-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open8"</span>&gt;</span>错误<span class="hljs-tag">&lt;/<span class="hljs-name">t-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
