</div></demo-block>
<h3>可关闭</h3>
<p>可以添加关闭按钮。</p>
<p>默认的 Alert 是可以被人工关闭的，设置<code v-pre="" class="code_inline">0</code>为不会被自动关闭，默认为 3000 毫秒。</p>
<h3>以组件的形式使用</h3>
<p>Alert组件是可以用组件的形式使用的</p>
<div class="demo-block">
  <div>
    <t-button type="button" @click="show=!show">组件使用方式</t-button>
    <t-alert type="warning" :closable="true" :show="show">组件使用方式,警告框</t-alert>
  </div>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <div>
    <t-button type="button" @click="show=!show">组件使用方式</t-button>
    <t-alert type="warning" :closable="true" :show="show">组件使用方式,警告框</t-alert>
  </div>
</div>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">t-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"show=!show"</span>&gt;</span>组件使用方式<span class="hljs-tag">&lt;/<span class="hljs-name">t-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">t-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">:closable</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">:show</span>=<span class="hljs-string">"show"</span>&gt;</span>组件使用方式,警告框<span class="hljs-tag">&lt;/<span class="hljs-name">t-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>全局方法</h3>
<p>tty-ui 为 Vue.prototype 添加了全局方法 $alert。因此在 vue instance 中可以采用本页面中的方式调用 <code v-pre="" class="code_inline">TAlert</code>。</p>
<h3>单独引用</h3>
<p>单独引入 <code v-pre="" class="code_inline">TAlert</code>：</p>
<pre v-pre=""><code v-pre="" class="language-javascript"><span class="hljs-keyword">import</span> { TAlert } <span class="hljs-keyword">from</span> <span class="hljs-string">'tty-ui'</span>;

</code></pre>
<h3>Options or Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>message</td>
<td>消息文字</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>type</td>
<td>主题</td>
<td>string</td>
<td>success/warning/info/danger</td>
<td>info</td>
</tr>
<tr>
<td>duration</td>
<td>显示时间, 毫秒。设为 0 则不会自动关闭</td>
<td>number</td>
<td>—</td>
<td>3000</td>
</tr>
<tr>
<td>closable</td>
<td>是否显示关闭按钮</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Methods</h3>
<p>调用 <code v-pre="" class="code_inline">Alert</code> 或 <code v-pre="" class="code_inline">this.$alert</code> 会返回当前 Alert 的实例。如果需要手动关闭实例，可以调用它的 <code v-pre="" class="code_inline">close</code> 方法。</p>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>关闭当前的 Message</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  module.exports = {
    data () {
      return {
        show: false
      }
    },
    methods: {
      open() {
        this.$alert('这是一条消息提示');
      },

      open2() {
        this.$alert({
          message: '恭喜你，这是一条成功消息',
          type: 'success'
        });
      },

      open3() {
        this.$alert({
          message: '警告哦，这是一条警告消息',
          type: 'warning'
        });
      },

      open4() {
        this.$alert({
          showClose: true,
          message: '错了哦，这是一条错误消息',
          type: 'danger'
        });
      },

      open5() {
        this.$alert({
          type: 'success',
          message: '恭喜你，打开一个提示框',
          duration: 3000,
          closable: false
        });
      },

      open6() {
        this.$alert({
          showClose: true,
          message: '恭喜你，这是一条成功消息',
          type: 'success'
        });
      },

      open7() {
        this.$alert({
          showClose: true,
          message: '警告哦，这是一条警告消息',
          type: 'warning'
        });
      },

      open8() {
        this.$alert({
          showClose: true,
          message: '错了哦，这是一条错误消息',
          type: 'danger'
        });
      },

      openCenter() {
        this.$alert({
          message: '居中的文字',
          center: true
        });
      },

      openHTML() {
        this.$alert({
          dangerouslyUseHTMLString: true,
          message: '<strong>这是 <i>HTML</i> 片段</strong>'
        });
      }
    }
  };
</script>


