<template><section>
<h1>Select 下拉选择器</h1>
<p>当选项过多时，使用下拉菜单展示并选择内容。</p>
<hr>
<h3>基础用法</h3>
<p>适用广泛的基础单选。
<code v-pre="" class="code_inline">v-model</code>的值为当前被选中的<code v-pre="" class="code_inline">t-option</code>的 value 属性值</p>
<div class="demo-block">
  <t-select v-model="selectValue" placeholder="请选择">
    <t-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></t-option>
  </t-select>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><t-select v-model="selectValue" placeholder="请选择">
  <t-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></t-option>
</t-select>



</body></html></div>
