<span class="hljs-tag">&lt;/<span class="hljs-name">t-select</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {

    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">selectValue2</span>:<span class="hljs-string">''</span>,
        <span class="hljs-attr">selectValue2</span>:<span class="hljs-string">''</span>,
        <span class="hljs-attr">options2</span>: [{
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项1'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'深圳南山'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项2'</span>,
