          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>,
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项3'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'上海滩外'</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>,
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项4'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'杭州西湖'</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>,
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项5'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'广州天河'</span>
