<h3>可清空单选</h3>
<p>包含清空按钮，可将选择器清空为初始状态</p>
<p>为<code v-pre="" class="code_inline">t-select</code>设置<code v-pre="" class="code_inline">clearable</code>属性，则可将选择器清空。需要注意的是，<code v-pre="" class="code_inline">clearable</code>属性仅适用于单选。</p>
<div class="demo-block">
  <t-select v-model="selectValue3" clearable="" placeholder="请选择">
    <t-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value"></t-option>
  </t-select>
</div>
<demo-block>
