<h3>基础多选</h3>
<p>适用性较广的基础多选，用 Tag 展示已选项</p>
<p>为<code v-pre="" class="code_inline">t-select</code>设置<code v-pre="" class="code_inline">multiple</code>属性即可启用多选，此时<code v-pre="" class="code_inline">v-model</code>的值为当前选中值所组成的数组。默认情况下选中值会以 Tag 的形式展现，你也可以设置<code v-pre="" class="code_inline">collapse-tags</code>属性将它们合并为一段文字。</p>
<div class="demo-block">
  <t-select v-model="selectMulValue" multiple placeholder="请选择">
    <t-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></t-option>
  </t-select>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><t-select v-model="selectValue" placeholder="请选择">
  <t-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></t-option>
</t-select>



</body></html></div>
