<h3>可搜索</h3>
<p>可以利用搜索功能快速查找选项
为<code v-pre="" class="code_inline">t-select</code>添加<code v-pre="" class="code_inline">filterable</code>属性即可启用搜索功能。默认情况下，Select 会找出所有<code v-pre="" class="code_inline">label</code>属性包含输入值的选项。如果希望使用其他的搜索逻辑，可以通过传入一个<code v-pre="" class="code_inline">filter-method</code>来实现。<code v-pre="" class="code_inline">filter-method</code>为一个<code v-pre="" class="code_inline">Function</code>，它会在输入值发生变化时调用，参数为当前输入值。</p>
<div class="demo-block">
  <t-select v-model="selectValue" filterable="" placeholder="请选择">
    <t-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></t-option>
  </t-select>
</div>
<demo-block>
